export const findNodeWithText = ({
  node,
  searchText,
  level = 0,
  maxLevel = 2,
}) => {
  if (level > maxLevel) {
    return null; // Stop searching beyond the maximum level
  }

  if (node && node?.textContent?.includes(searchText)) {
    return node; // Found the node with the text
  }

  let foundNode = null;
  Array.from(node?.children || []).some((child) => {
    foundNode = findNodeWithText(child, searchText, level + 1, maxLevel);
    return foundNode; // Stop iteration if a node is found
  });

  return foundNode;
};
