import {
  LINKEDIN_SIDEBAR_TOGGLE,
  REFRESH_COOKIES,
  SHOW_TOAST_ON_WEBSITE,
} from "../constants/messageConstants";
import { EXTENSION_WEBSITE_URL } from "../constants/urls";
import { waitForTransitionEnd } from "../utils/browser";
import { sendMessageWithData } from "../utils/sendMessage";
import initializeSentry from "../sentry/initialize";
import { addToast } from "../utils/autoApply";
import {
  parseJobDetailsFromFeedPreview,
  parseJobDetailsFromJobsTab,
  parseJobDetailsFromRecommendedJobsInFeed,
  parseJobDetailsFromSimilarJobs,
} from "./utils/linkedinJobParser";
import {
  addEasyApplyButtonAndSaveJobDetails,
  updateJobDetails,
} from "./utils/linkedinJobsScrapper";
import packageJson from "../../package.json";
import { chromeLocalStorage } from "../utils/chromeStorage";
import { CS_INSTALLATION_PARAMS } from "../constants/chromeStorageConstants";
import { CACHE_CONFIG, getCachedResponse } from "../utils/cacheApiResponse";
import { getFirebaseRemoteConfigObj } from "../api/internalServices";
import { checkObjectSanity, isNullUndefinedOrEmpty } from "../utils/lodash";
import { LINKEDIN_PARSER_CONFIG } from "../constants/commonConstants";

initializeSentry();
let parserConfig;

// refresh cookies
sendMessageWithData(REFRESH_COOKIES);

let iframe = null;
let slidingInProgress = false;
const isFrameHidden = (frame) => frame.style.display === "none";
let isLoaded = false;
let openSidebar = false;

window.addEventListener("load", () => {
  isLoaded = true;
  if (openSidebar) {
    openSidebar = false;
    toggleIframe();
  }
});

const escapeKeyHandler = (e) => {
  // Escape
  if (e.keyCode === 27) {
    toggleIframe();
  }
};

const setupDocumentListeners = () => {
  document.addEventListener("mousedown", toggleIframe);
  document.addEventListener("keydown", escapeKeyHandler);
};

const removeDocumentListeners = () => {
  document.removeEventListener("mousedown", toggleIframe);
  document.removeEventListener("keydown", escapeKeyHandler);
};

const slideInIframe = () => {
  slidingInProgress = true;
  requestAnimationFrame(() => {
    iframe.style.display = "block";

    // Delayed for transition to trigger
    requestAnimationFrame(() => {
      iframe.style.transform = "translateX(0%)";
      iframe.style.opacity = "1";
    });

    waitForTransitionEnd(iframe).then(() => {
      slidingInProgress = false;
      setupDocumentListeners();
    });
  });
};

const slideOutIframe = () => {
  slidingInProgress = true;
  requestAnimationFrame(() => {
    iframe.style.transform = "translateX(100%)";
    iframe.style.opacity = "0";

    waitForTransitionEnd(iframe).then(() => {
      iframe.style.display = "none";
      slidingInProgress = false;
      removeDocumentListeners();

      // Remove iframe on slide out
      document.body.removeChild(iframe);
      iframe = null;
    });
  });
};

const generateIframe = async () => {
  iframe = document.createElement("iframe");

  let popupUrl = `${EXTENSION_WEBSITE_URL}/popup?extensionVersion=${packageJson?.version}`;
  const { circleReferrerUserId, circleReferrerOrganizationId } =
    await chromeLocalStorage.getItemAsync(CS_INSTALLATION_PARAMS);
  if (circleReferrerUserId && circleReferrerOrganizationId) {
    popupUrl += `&circleReferrerUserId=${circleReferrerUserId}&circleReferrerOrganizationId=${circleReferrerOrganizationId}`;
  }
  // Styles from google keep extension
  iframe.frameBorder = 0;
  iframe.src = popupUrl;
  iframe.id = "weekday_extn";
  iframe.style.height = "100%";
  iframe.style.margin = "0px";
  iframe.style.padding = "0px";
  iframe.style.position = "fixed";
  iframe.style.right = "0px";
  iframe.style.top = "0px";
  iframe.style.width = "400px";
  iframe.style.zIndex = "2148372";
  iframe.style.background = "#f2f2f2";
  iframe.style.boxShadow = "-4px 0 30px 15px rgba(0,0,0,.2)";
  iframe.style.display = "none";
  iframe.style.opacity = "0";
  iframe.style.transition =
    "transform 0.25s ease-in-out, opacity 0.25s ease-in-out";
  iframe.style.transform = "translateX(100%)";
  document.body.appendChild(iframe);

  requestAnimationFrame(() => {
    slideInIframe();
  });
};

function toggleIframe() {
  if (slidingInProgress) {
    return;
  }
  if (!getIframeFromBody()) {
    generateIframe();
  } else if (isFrameHidden(iframe)) {
    slideInIframe();
  } else {
    slideOutIframe();
  }
}

function getIframeFromBody() {
  return document.getElementById("weekday_extn");
}

// Easy apply buttons
const addEasyApplyButtonToFeed = () => {
  if (
    isNullUndefinedOrEmpty(
      parserConfig?.cardSelectors?.addEasyApplyButtonToFeed
    )
  ) {
    return;
  }
  const selector = parserConfig?.cardSelectors?.addEasyApplyButtonToFeed;
  const jobCarousel = [...document.querySelectorAll(selector)];
  addEasyApplyButtonAndSaveJobDetails({
    cards: jobCarousel,
    parser: parseJobDetailsFromRecommendedJobsInFeed(parserConfig),
    buttonStyles: {
      margin: "0 0 0 4px",
    },
  });
};

const addEasyApplyButtonToJobsTab = () => {
  if (
    isNullUndefinedOrEmpty(
      parserConfig?.cardSelectors?.addEasyApplyButtonToJobsTab
    )
  ) {
    return;
  }
  const selector = parserConfig?.cardSelectors?.addEasyApplyButtonToJobsTab;
  const jobList = [...document.querySelectorAll(selector)];
  addEasyApplyButtonAndSaveJobDetails({
    cards: jobList,
    parser: parseJobDetailsFromJobsTab(parserConfig),
    parentStyles: {
      alignItems: "center",
    },
  });
};

const addEasyApplyButtonToJobsSidebar = () => {
  if (
    isNullUndefinedOrEmpty(
      parserConfig?.cardSelectors?.addEasyApplyButtonToJobsSidebar
    )
  ) {
    return;
  }
  const selector = parserConfig?.cardSelectors?.addEasyApplyButtonToJobsSidebar;
  const jobList = [...document.querySelectorAll(selector)];
  addEasyApplyButtonAndSaveJobDetails({
    cards: jobList,
    parser: parseJobDetailsFromJobsTab(parserConfig),
  });
};

const addEasyApplyButtonToSimilarJobs = () => {
  if (
    isNullUndefinedOrEmpty(
      parserConfig?.cardSelectors?.addEasyApplyButtonToSimilarJobs
    )
  ) {
    return;
  }
  const selector = parserConfig?.cardSelectors?.addEasyApplyButtonToSimilarJobs;
  const jobList = [...document.querySelectorAll(selector)];
  addEasyApplyButtonAndSaveJobDetails({
    cards: jobList,
    parser: parseJobDetailsFromSimilarJobs(parserConfig),
    buttonStyles: {
      margin: "6px 4px",
    },
    parentStyles: {
      alignItems: "center",
    },
    skipSavingJobToDb: true,
  });
};

const addEasyApplyButtonToFeedPreview = () => {
  if (
    isNullUndefinedOrEmpty(
      parserConfig?.cardSelectors?.addEasyApplyButtonToFeedPreview
    )
  ) {
    return;
  }
  const selector = parserConfig?.cardSelectors?.addEasyApplyButtonToFeedPreview;
  const jobList = [...document.querySelectorAll(selector)];
  addEasyApplyButtonAndSaveJobDetails({
    cards: jobList,
    parser: parseJobDetailsFromFeedPreview(parserConfig),
    buttonStyles: {
      position: "absolute",
      right: "8px",
      bottom: "4px",
    },
    skipSavingJobToDb: true,
  });
};

const getSelectorsFromRemoteConfig = async () => {
  const remoteConfig = await getCachedResponse(
    getFirebaseRemoteConfigObj,
    CACHE_CONFIG.REMOTE_CONFIG
  );
  try {
    const config = JSON.parse(
      remoteConfig?.[LINKEDIN_PARSER_CONFIG]?.defaultValue?.value
    );
    if (checkObjectSanity(config)) {
      parserConfig = config;
    }
  } catch (e) {}
};

getSelectorsFromRemoteConfig();

setInterval(() => {
  if (checkObjectSanity(parserConfig)) {
    addEasyApplyButtonToFeed();
    addEasyApplyButtonToJobsTab();
    addEasyApplyButtonToJobsSidebar();
    addEasyApplyButtonToSimilarJobs();
    addEasyApplyButtonToFeedPreview();
    updateJobDetails(parserConfig);
  }
}, 4000);

chrome.runtime.onMessage.addListener(function (message, sender, sendresponse) {
  switch (message.type) {
    case LINKEDIN_SIDEBAR_TOGGLE: {
      if (isLoaded) {
        toggleIframe();
      } else {
        openSidebar = true;
      }
      sendresponse("done");
      break;
    }
    case SHOW_TOAST_ON_WEBSITE: {
      addToast(message.data);
      sendresponse("done");
      break;
    }
  }
});
