import { saveLinkedInJobsFromExtension } from "../../api/adhoc";
import { EXTENSION_WEBSITE_URL } from "../../constants/urls";
import { findNodeWithText } from "../../utils/dom";
import { checkArraySanity, forEach } from "../../utils/lodash";
import packageJson from "../../../package.json";
import { parseJobDetailsFromJobDescription } from "./linkedinJobParser";
import { chromeLocalStorage } from "../../utils/chromeStorage";
import {
  CS_PREV_LINKEDIN_ID,
  CS_PREV_LINKEDIN_ID_SIDEBAR,
} from "../../constants/chromeStorageConstants";
import { EMPTY_ARRAY } from "../../constants/common";

const WEEKDAY_EASY_APPLY_BUTTON_LINK = (linkedInJobId) =>
  `${EXTENSION_WEBSITE_URL}/autoApply?linkedInJobId=${linkedInJobId}&extensionVersion=${packageJson?.version}`;

const EXTENSION_JOB_SOURCE = {
  LINKEDIN_EASY_APPLY: "linkedin_extension_easy_apply",
  LINKEDIN_CAREER_PAGES: "linkedin_extension",
};

const EASY_APPLY_IFRAME_STYLES = {
  width: "96px",
  height: "28px",
  borderRadius: "20px",
  zIndex: "1",
  margin: "0 4px",
};

/* Keep a list of jobs that have been scraped and don't call the save API again. 
  Clear the list after 3000 entries */
const updatePreviousScrapedJobsList = async (
  previousLinkedInJobsScraped,
  source
) => {
  await chromeLocalStorage.setItemAsync(
    source,
    previousLinkedInJobsScraped.size < 3000
      ? Array.from(previousLinkedInJobsScraped)
      : EMPTY_ARRAY
  );
};

const addWeekdayEasyApplyButton = ({ parent, linkedInJobId, styles = {} }) => {
  const button = document.createElement("iframe");
  button.src = WEEKDAY_EASY_APPLY_BUTTON_LINK(linkedInJobId);
  button.classList.add("weekday-easy-apply");

  forEach(
    {
      ...EASY_APPLY_IFRAME_STYLES,
      ...styles,
    },
    (val, key) => {
      button.style[key] = val;
    }
  );

  parent.append(button);
};

export const addEasyApplyButtonAndSaveJobDetails = async ({
  cards,
  parser,
  buttonStyles = {},
  parentStyles = {},
  skipSavingJobToDb,
}) => {
  if (checkArraySanity(cards)) {
    const prevLinkedInJobIds = await chromeLocalStorage.getItemAsync(
      CS_PREV_LINKEDIN_ID_SIDEBAR
    );
    const previousLinkedInJobsScraped = new Set(
      checkArraySanity(prevLinkedInJobIds) ? prevLinkedInJobIds : null
    );
    for (const card of cards) {
      if (!card.querySelector(".weekday-easy-apply")) {
        const {
          parent,
          imageUrl,
          jobTitle,
          companyName,
          jobLocation,
          jdUrl,
          isActivelyHiring,
          linkedInJobId,
        } = parser(card);
        const isLinkedinEasyApplyJob = findNodeWithText({
          node: parent,
          searchText: "Easy Apply",
          maxLevel: 5,
        });
        if (!parent || !jdUrl) continue; // not a job card

        if (!isLinkedinEasyApplyJob) {
          forEach(parentStyles, (val, key) => {
            parent.style[key] = val;
          });

          addWeekdayEasyApplyButton({
            linkedInJobId,
            parent,
            styles: buttonStyles,
          });
        } else {
          parent.classList.add("weekday-easy-apply");
        }

        if (
          !skipSavingJobToDb &&
          !previousLinkedInJobsScraped.has(linkedInJobId)
        ) {
          previousLinkedInJobsScraped.add(linkedInJobId ?? "");

          try {
            saveLinkedInJobsFromExtension({
              jobTitle,
              companyName,
              jobLocation,
              companyLogoUrl: imageUrl,
              jobUrl: jdUrl,
              linkedInJobId,
              isActivelyHiring,
              source: isLinkedinEasyApplyJob
                ? EXTENSION_JOB_SOURCE.LINKEDIN_EASY_APPLY
                : EXTENSION_JOB_SOURCE.LINKEDIN_CAREER_PAGES,
            });
          } catch (err) {
            console.error(err);
          }
        }
      }
    }
    await updatePreviousScrapedJobsList(
      previousLinkedInJobsScraped,
      CS_PREV_LINKEDIN_ID_SIDEBAR
    );
  }
};

export const updateJobDetails = async (parserConfig) => {
  const selectors = parserConfig?.parseJobDetailsFromJobDescription;
  const card = document.querySelector(selectors?.card);
  if (!card) return;

  const {
    jobTitle,
    companyName,
    skillTags,
    pocLinkedInUrl,
    poc,
    jdUrl,
    pocDesignation,
    jobDescription,
    linkedInJobId,
    companySize,
    companySizeOnLinkedIn,
    companyLinkedInUrl,
  } = parseJobDetailsFromJobDescription(card, selectors);
  if (!companyName || !jobTitle) return;

  const prevLinkedInJobId = await chromeLocalStorage.getItemAsync(
    CS_PREV_LINKEDIN_ID
  );

  const previousLinkedInJobsScraped = new Set(
    checkArraySanity(prevLinkedInJobId) ? prevLinkedInJobId : null
  );

  if (!previousLinkedInJobsScraped.has(linkedInJobId)) {
    previousLinkedInJobsScraped.add(linkedInJobId);

    await updatePreviousScrapedJobsList(
      previousLinkedInJobsScraped,
      CS_PREV_LINKEDIN_ID
    );

    try {
      saveLinkedInJobsFromExtension({
        jobUrl: jdUrl,
        jobTitle,
        companyName,
        skillTags,
        pocLinkedInUrl,
        poc,
        pocDesignation,
        jobDescription,
        linkedInJobId,
        companySize,
        companySizeOnLinkedIn,
        companyLinkedInUrl,
        source: findNodeWithText({
          node: document.querySelectorAll(".jobs-apply-button")?.[0],
          searchText: "Easy Apply",
          maxLevel: 5,
        })
          ? EXTENSION_JOB_SOURCE.LINKEDIN_EASY_APPLY
          : EXTENSION_JOB_SOURCE.LINKEDIN_CAREER_PAGES,
      });
    } catch (err) {
      console.error(err);
    }
  }
};
