import { checkArraySanity, isNullUndefinedOrEmpty } from "../../utils/lodash";

const UNIMPORTANT_URL_PARAMS = [
  "eBP",
  "refId",
  "trackingId",
  "lipi",
  "lici",
  "trk",
];

export const linkedinJobUrlParser = (url) => {
  try {
    const jobUrl = new URL(url);
    UNIMPORTANT_URL_PARAMS.forEach((param) => {
      jobUrl.searchParams.delete(param);
    });
    return jobUrl.toString();
  } catch (err) {
    return url;
  }
};

const extractJobIdFromJobUrl = (jobUrl) => {
  const regex = /\/jobs\/view\/(\d+)/;
  const match = jobUrl?.match(regex);

  if (match && match[1]) {
    return match[1];
  }
  try {
    const url = new URL(jobUrl);
    const jobID = url.searchParams.get("currentJobId");
    if (jobID) return jobID;
  } catch (err) {}
  return null;
};

export const parseJobDetailsFromRecommendedJobsInFeed =
  (parserConfig) => (card) => {
    const selectors = parserConfig?.parseJobDetailsFromRecommendedJobsInFeed;
    const parent = card.querySelector(selectors?.parent);
    const imageUrl = card.querySelector(selectors?.imageUrl)?.src;
    const jobTitle = card.querySelector(selectors?.jobTitle)?.innerText;
    const companyName = card.querySelector(selectors?.companyName)?.innerText;
    const jobLocation = card.querySelector(selectors?.jobLocation)?.innerText;
    const jdUrl = card?.href;
    const parsedUrl = jdUrl ? linkedinJobUrlParser(jdUrl) : jdUrl;

    return {
      parent,
      imageUrl,
      jobTitle,
      companyName,
      jobLocation,
      jdUrl: parsedUrl,
      linkedInJobId: extractJobIdFromJobUrl(parsedUrl),
    };
  };

export const parseJobDetailsFromJobsTab = (parserConfig) => (card) => {
  const selectors = parserConfig?.parseJobDetailsFromJobsTab;
  const parent = card.querySelector(selectors?.parent);
  const imageUrl = card.querySelector(selectors?.imageUrl)?.src;
  const jobTitle = card.querySelector(selectors?.jobTitle)?.innerText;
  const jdUrl = card.querySelector(selectors?.jdUrl)?.href;
  const companyNameAndLocation = card.querySelector(
    selectors?.companyNameAndLocation
  )?.innerText;
  let companyName, jobLocation;
  if (companyNameAndLocation) {
    const strArr = companyNameAndLocation?.split("")?.map((s) => s?.trim());
    if (checkArraySanity(strArr)) {
      companyName = strArr?.[0];
      jobLocation = strArr?.[1] || null;
    }
  }
  if (isNullUndefinedOrEmpty(jobLocation)) {
    jobLocation = card.querySelector(selectors?.jobLocation)?.innerText;
  }
  const isActivelyHiring = card.querySelector(selectors?.isActivelyHiring)
    ? 1
    : 0;

  const parsedUrl = jdUrl ? linkedinJobUrlParser(jdUrl) : jdUrl;

  return {
    parent,
    imageUrl,
    jobTitle,
    companyName,
    jobLocation,
    jdUrl: parsedUrl,
    isActivelyHiring,
    linkedInJobId: extractJobIdFromJobUrl(parsedUrl),
  };
};

export const parseJobDetailsFromJobDescription = (card, selectors) => {
  const parent = card.querySelector(selectors?.parent)?.parentElement;
  const jobTitle = card.querySelector(selectors?.jobTitle)?.innerText;
  const jdUrl = card.querySelector(selectors?.jobTitle)?.children?.[0]?.href;
  const companyName = card.querySelector(selectors?.companyName)?.innerText;

  const pocLinkedInUrl = card.querySelector(selectors?.pocLinkedIn)?.href;
  const poc = card.querySelector(selectors?.pocName)?.innerText;
  const pocDesignation = parent?.querySelector(selectors?.pocRole)?.innerText;
  const jobDescription = card.querySelector(
    selectors?.jobDescription
  )?.innerText;
  const skillsText = card.querySelector(selectors?.skillsText)?.innerText;

  const companyLinkedInUrl = card
    .querySelector(selectors?.companyLinkedInUrl)
    ?.href?.replace("/life", "");

  const companySizeSelectors = card.querySelectorAll(selectors?.companySize);
  let companySize, companySizeOnLinkedIn;
  companySizeSelectors?.forEach((selector) => {
    if (selector?.innerText?.includes("on LinkedIn")) {
      companySizeOnLinkedIn = selector?.innerText?.trim();
    } else if (selector?.innerText?.includes("employees")) {
      companySize = selector?.innerText?.trim();
    }
  });

  const skillTags = skillsText
    ? skillsText?.split?.("")?.map((s) => s?.trim())
    : [];
  [...card.querySelectorAll(selectors?.skillsRequired)]
    ?.map((el) => el?.innerText)
    ?.forEach((text) => {
      skillTags?.push?.(...text?.split(",")?.map((s) => s?.trim()));
    });
  const parsedUrl = jdUrl ? linkedinJobUrlParser(jdUrl) : jdUrl;
  return {
    jobTitle,
    companyName,
    skillTags,
    pocLinkedInUrl,
    poc,
    jdUrl: parsedUrl,
    pocDesignation,
    jobDescription,
    linkedInJobId: extractJobIdFromJobUrl(parsedUrl),
    companySize,
    companySizeOnLinkedIn,
    companyLinkedInUrl,
  };
};

